import React from "react";
import { Link } from "react-router-dom";
import { useKeycloak } from "@react-keycloak/web";
import Answer from '../helpers/Answer';
import './RobloxPage.css';
import '../App.css';

const TicksterPage = () => {
    const { keycloak } = useKeycloak()
    console.log("idToken: ", keycloak.idTokenParsed)

    return (
        <div class="page" id="roblox">
            <div id="header">
                <img src="https://images.rbxcdn.com/1c79d755265947bd1922c652845eea91" alt="" height="40px"/>
                {
                    !keycloak.authenticated
                        ? <button id="login-button" onClick={() => keycloak.login()}>Login</button>
                        : <button id="logout-button" onClick={() => keycloak.logout()}>Logout</button>
                }
            </div>
            <div class="content-wrapper">
                <div class="content">
                    {
                        !keycloak.authenticated ? 
                            <h1>Välkommen till Tickster</h1> : 
                            <div>
                                <h2>Välkommen {keycloak.idTokenParsed.given_name}</h2>
                                <h3>Dina uppgifter</h3>
                                <ul>
                                    <li><Answer title="Förnamn" value={keycloak.idTokenParsed.given_name} /></li>
                                    <li><Answer title="Efternamn" value={keycloak.idTokenParsed.family_name} /></li>
                                    <li><Answer title="E-post" value={keycloak.idTokenParsed.email} /></li>
                                </ul>
                            </div>
                    }
                </div>
            </div>
            <div id="footer">
                <button id="back"><Link to="/">Tillbaka</Link></button>
            </div>
        </div>
    )
}

export default TicksterPage;
